.home {
    width              : 100%;
    height             : 90vh;
    display            : flex;
    justify-content    : center;
    align-items        : left;
    flex-direction     : column;
    background-position: center;
    background-repeat  : no-repeat;
    background-size    : cover;
    font-family        : "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;

    .headerContainer {
        width      : auto;
        margin-left: 50px;

        h1 {
            justify-content: center;
            align-items    : center;
            font-size      : 90px;

            font-weight: 50;
            color      : black;
        }

        p {
            padding    : 0;
            font-size  : 40px;
            font-weight: lighter;
            color      : black;
        }

        button {
            color           : black;
            border          : none;
            border-radius   : 5px;
            cursor          : pointer;
            justify-content : center;
            background-color: #121619;
            height          : 50px;
            width           : 180px;
            color           : white;
            text-decoration : none;
            font-size       : 20px;

            &:hover {
                background-color: #07090a;
                cursor          : pointer;
                transition      : 0.3s ease-in;
            }
        }
    }
}

@media screen and (max-width:650px) {
    .home {
        justify-content: center;
        align-items    : center;

        .headerContainer {
            margin-left     : 0px;
            border-radius   : 10px;
            padding         : 10px;
            width           : 80%;
            display         : flex;
            justify-content : center;
            align-items     : center;
            flex-direction  : column;
            background-color: rgba(0, 0, 0, 0.4);
            color           : white;

            h1 {
                font-size: 40px;

                color: rgb(250, 250, 250);
            }

            p {
                font-size: 30px;
                color    : white;
            }

            button {
                background-color: white;
                color           : #121619;
                margin-bottom   : 30px;

                &:hover {
                    background-color: rgb(107, 158, 25);
                    color           : #121619;
                }
            }
        }
    }
}